<template>
    <div>
        <b-form-textarea
            v-model="ucapan"
            placeholder="Silakan titipkan doa anda disini.."
            rows="3"
            max-rows="6"
            @keyup.enter.prevent="addItem">
        </b-form-textarea>
        <b-form-input 
            placeholder="Isikan nama anda.." 
            v-model="name"
            @keyup.enter.prevent="addItem">
        </b-form-input>
        <b-button variant="outline-primary" @click.prevent="addItem">Tambah</b-button>
        <div>

        </div>

        <b-card
            tag="article"
            style="max-width: 20rem;"
            class="mb-2"
            v-for="data in datas"
            :key="data.id"
        >
            <b-card-text>
                <i>"{{ data.ucapan }}"</i> 
                <br>{{ data.name }}
            </b-card-text>
        </b-card>
    </div>
</template>

<script>
import { db } from "../firebase/db";

export default {
    data() {
      return {
        datas: [],
        name: '',
        ucapan:'',
      }
    },
    methods: {
        deleteToDo(id) {
        db.collection("testing")
            .doc(id)
            .delete();
        },
        async addItem() {
            if (this.name && this.ucapan) {
                await db.collection("testing").add({ name: this.name, ucapan:this.ucapan });
                this.name = "";
                this.ucapan = "";
            }
        },
    },
    firestore: {
        datas: db.collection("testing")
    }

}
</script>